// dependencies
import { H, getAPI, showLoader, getCookie, hideLoader, initHeader, initBreadcrumbs, parseApiError, onClick, onChange, simulateClick, spaceID, toast, link } from '@kenzap/k-cloud';
import { initFooter } from "../_/_helpers.js"
import { HTMLContent } from "../_/_cnt_home.js"

/**
 * Represents the Home class.
 * This class handles the locales localisation home page.
 * 
 * @link https://translate.kenzap.cloud/?sid=1002170
 * @since 1.2.0
 *
 * @package Translate
 */
class Home {

    constructor() {

        this.state = {
            firstLoad: true,
            html: '',
            data: {},
            ajaxQueue: 0
        }

        this.init();
    }

    init = () => {
        
        this.data();
    }

    data = () => {

        // show loader during first load
        if (this.state.firstLoad) showLoader();

        // do API query
        fetch(getAPI(), {
            method: 'post',
            headers: H(),
            body: JSON.stringify({
                query: {
                    locale: {
                        type:       'locale',
                        id:         getCookie('lang')
                    },
                    dashboard: {
                        type:       'dashboard',
                    }
                }
            })
        })
        .then(response => response.json())
        .then(response => {

            if(response.success){

                // init header
                initHeader(response);

                // get core html content 
                this.page();  

                // render table
                this.render(response);

                // listeners
                this.listeners();

                // initiate footer
                initFooter(this);

                // first load
                this.state.firstLoad = false;

                // console.log(response);

            }else{

                parseApiError(response);
            }
        })
        .catch(error => {
            console.error('Error:', error);
        });
    }

    getExtensions = () => {
        
        // console.log('getQRHTML');

        // init params
        let params = new URLSearchParams();
        params.append("cmd", "preview_extensions");
        params.append("s", '');
        params.append("token", getCookie('kenzap_token'));

        // do API query
        fetch('https://siteapi.kenzap.cloud/v1/', {
            method: 'post',
            headers: {
                'Accept': 'application/json',
                'Content-type': 'application/x-www-form-urlencoded',
            },
            body: params
        })
        .then(response => response.json())
        .then(response => {

            if(response.success){
            
                console.log(response);

                    // let html = '', key = 0;
                    // html += '<div class="row">';
                    // for (let row of response.res) {

                    //     // check if extension already added
                    //     html += '<div class="col-md-6" style="margin:16px 0;">';
                    //     html += '<h4>'+row['extra']['title'];
                    //     html += '<div class="br-wrapper br-theme-css-stars"><select id="profile-rating" name="rating" autocomplete="off" style="display: none;"><option value="1">1</option><option value="2">2</option><option value="3">3</option><option value="4">4</option><option value="5">5</option></select><div class="br-widget"><a href="#" data-rating-value="1" data-rating-text="1" class="br-selected"></a><a href="#" data-rating-value="2" data-rating-text="2" class="br-selected"></a><a href="#" data-rating-value="3" data-rating-text="3" class="br-selected"></a><a href="#" data-rating-value="4" data-rating-text="4" class="br-selected"></a><a href="#" data-rating-value="5" data-rating-text="5" class="br-selected br-current"></a></div></div>';
                    //     html += '</h4>';
                    //     html += '<img alt="'+row['extra']['title']+'" style="max-width:100%;" src="'+CDN2+'/preview/'+row['id']+'-600.jpeg?'+row['extra']['updated']+'" />';
                    //     if(_this.state.ext_ids.includes(row['id'])){
                    //         html += '<span class="csection" data-id="'+row['extra']['slug']+'" data-index="'+key+'" >' + __('Already added') + '</a>';
                    //     }else{
                    //         html += '<a class="sclick csection" data-id="'+row['extra']['slug']+'" data-index="'+key+'" >' + __('Choose this extensions') + '</a>';
                    //     }
                    //     html += '</div>';
                    //     key++;
                    // }
                    // html += '</div>';

                    // modal.querySelector(".modal-body").innerHTML = html;

                    // _this.state.modalCont.show();

                    // onClick('.sclick', _this.listeners.sectionClick);

            }else{

                // simulateClick(document.querySelector('.modal .btn-close'));
                // toast(__('No available extensions found.'));
                // parseApiError(response);
            }
        })
        .catch(error => {
            console.error('Error:', error);
        });
    }

    render = (response) => {

        // initiate breadcrumbs
        initBreadcrumbs(
            [
                { link: link('https://dashboard.kenzap.cloud?launcher=translate'), text: __('Dashboard') },
                { text: __('Translate') },
            ]
        );

        this.state.dashboard = response.dashboard;

        // init table header
        document.querySelector(".table thead").innerHTML = `
        <tr>
            <th>${ __("Extension") }</th>
            <th>${ __("") }</th>
        </tr>`;

        // render table rows
        let rows = "";
        Object.keys(response.dashboard.extensions).forEach((el) => {

            if(!response.dashboard.extensions[el].links) return;

            rows += `
            <tr">
                <td class="py-3">
                    ${ response.dashboard.extensions[el].links[0].icon } <a href="${ link('/edit/?ext='+el+'&slug='+response.dashboard.extensions[el].slug) }">${ response.dashboard.extensions[el].slug }</a>
                </td>
                <td class="text-end"> 
                    <a href="${ link('/edit/?ext='+el+'&slug='+response.dashboard.extensions[el].slug) }" data-id="${ link('/edit/?ext='+el) }" class="edit-ext text-dark ">
                        <svg xmlns="http://www.w3.org/2000/svg" width="16" height="16" fill="currentColor" class="bi bi-pencil-square me-2" viewBox="0 0 16 16">
                            <path d="M15.502 1.94a.5.5 0 0 1 0 .706L14.459 3.69l-2-2L13.502.646a.5.5 0 0 1 .707 0l1.293 1.293zm-1.75 2.456-2-2L4.939 9.21a.5.5 0 0 0-.121.196l-.805 2.414a.25.25 0 0 0 .316.316l2.414-.805a.5.5 0 0 0 .196-.12l6.813-6.814z"/>
                            <path fill-rule="evenodd" d="M1 13.5A1.5 1.5 0 0 0 2.5 15h11a1.5 1.5 0 0 0 1.5-1.5v-6a.5.5 0 0 0-1 0v6a.5.5 0 0 1-.5.5h-11a.5.5 0 0 1-.5-.5v-11a.5.5 0 0 1 .5-.5H9a.5.5 0 0 0 0-1H2.5A1.5 1.5 0 0 0 1 2.5v11z"/>
                        </svg>
                    </a>
                </td>
            </tr>`; 
        });

        document.querySelector(".table tbody").innerHTML =  rows;

        hideLoader();
    }

    listeners = () => {

        // prevents listeners to be assigned twice
        if(!this.state.firstLoad) return;

        //...
    }

    page = () => {

        if(!this.state.firstLoad) return;

        // get core html content 
        document.querySelector('#contents').innerHTML = HTMLContent(__);
    }
}

new Home();
